import React, { ReactElement } from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";

import App from "./App";
import lazyComponent from "./components/LazyComponent";
import Home from "./pages/Home";
import CONST from "./constants/app";

interface T_Props {}

const Tools = lazyComponent("pages/Tools");
const ToolsCrypto = lazyComponent("pages/Tools/Crypto");
const Money = lazyComponent("pages/Tools/Money");
const CurrencyEx = lazyComponent("pages/Tools/Money/CurrencyEx");

export default function AppRoutes({}: T_Props): ReactElement {
  return (
    <BrowserRouter>
      <Routes>
        <Route path={CONST.pages.home.path} element={<App />}>
          <Route index element={<Home />} />
          <Route path={CONST.pages.tools.path} element={<Tools />} />
          <Route path={CONST.pages.toolsMoney.path} element={<Money />} />
          <Route
            path={CONST.pages.toolsMoneyEx.path}
            element={<CurrencyEx />}
          />
          <Route
            path={CONST.pages.toolsCrypto.path}
            element={<ToolsCrypto />}
          />
        </Route>
      </Routes>
    </BrowserRouter>
  );
}
